import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";

import * as actionCreators from "../../../store/actions/index";
import Button from "../../../components/UI/Button/Button";
import Spinner from "../../../components/UI/Spinner/Spinner";
import Input from "../../../components/UI/Input/Input";
import checkValidity from "../../../utilities/formsValidation";
import "./ContactData.css";

class ContactData extends Component {
  state = {
    orderForm: {
      name: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "John Doe"
        },
        value: "",
        validation: {
          required: true,
          maxLength: 25
        },
        valid: false,
        touched: false
      },
      email: {
        elementType: "input",
        elementConfig: {
          type: "email",
          placeholder: "johndoe@gmail.com"
        },
        value: "",
        validation: {
          required: true,
          maxLength: 20
        },
        valid: false,
        touched: false
      },
      address: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "Allee Unter den Linden"
        },
        value: "",
        validation: {
          required: true,
          maxLength: 50
        },
        valid: false,
        touched: false
      },
      zip: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "94024"
        },
        value: "",
        validation: {
          required: true,
          maxLength: 10
        },
        valid: false,
        touched: false
      },
      delivery: {
        elementType: "select",
        elementConfig: {
          options: [
            { value: "fast", text: "Fast" },
            { value: "cheap", text: "Cheap" }
          ]
        },
        value: "fast",
        validation: {},
        valid: true,
        touched: false
      }
    },
    formIsValid: false
  };

  changeHandler = (e, id) => {
    let formIsValid = true;
    let updatedForm = { ...this.state.orderForm };
    let updatedInputElement = { ...updatedForm[id] };
    updatedInputElement.value = e.target.value;
    updatedInputElement.touched = true;
    updatedInputElement.valid = checkValidity(
      updatedInputElement.value,
      updatedInputElement.validation
    );
    updatedForm[id] = updatedInputElement;
    for (let key in updatedForm) {
      formIsValid = updatedForm[key].valid && formIsValid;
    }
    this.setState({ orderForm: updatedForm, formIsValid: formIsValid });
  };

  submitHandler = e => {
    e.preventDefault();
    let customerData = {};
    for (let key in this.state.orderForm) {
      customerData[key] = this.state.orderForm[key].value;
    }
    let order = {
      ingredients: this.props.ingredients,
      price: this.props.totalPrice,
      customer: customerData,
      date: Date.now(),
      userId: this.props.userId
    };
    this.props.onPurchase(order, this.props.token);
  };

  render() {
    if (this.props.loading) {
      return <Spinner />;
    }
    let formContent = [];
    for (let key in this.state.orderForm) {
      formContent.push({
        data: { ...this.state.orderForm[key] },
        id: key
      });
    }
    return (
      <div className="ContactData">
        <h4>Enter your Contact Data</h4>
        <form onSubmit={this.submitHandler}>
          {formContent.map(elem => (
            <Input
              elementType={elem.data.elementType}
              key={elem.id}
              data={elem.data.elementConfig}
              value={elem.data.value}
              invalid={!elem.data.valid}
              shouldValidate={!!elem.data.validation}
              touched={elem.data.touched}
              change={e => this.changeHandler(e, elem.id)}
            />
          ))}
          <Button classes="Success" disabled={!this.state.formIsValid}>
            ORDER
          </Button>
        </form>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    ingredients: state.burgerBuilder.ingredients,
    totalPrice: state.burgerBuilder.totalPrice,
    loading: state.orders.loading,
    token: state.auth.token,
    userId: state.auth.userId
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onPurchase: (orderData, token) =>
      dispatch(actionCreators.asyncPurchaseBurger(orderData, token))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(ContactData));



