import React from "react";
import BurgerIngridients from "./BurgerIngridients/BurgerIngridients";
import { withRouter } from "react-router-dom";
import "./Burger.css";

const Burger = props => {
  let ingredients = Object.keys(props.ingredients)
    .map(elem => {
      return [...Array(props.ingredients[elem])].map((elem2, index) => {
        return <BurgerIngridients key={elem + index} type={elem} />;
      });
    })
    .reduce((accumulator, currentValue) => {
      return accumulator.concat(currentValue);
    }, []);
  if (!ingredients.length) {
    ingredients = <p>Start adding your ingredients!</p>;
  }
  return (
    <div className="Burger">
      <BurgerIngridients type="bread-top" />
      {ingredients}
      <BurgerIngridients type="bread-bottom" />
    </div>
  );
};

export default withRouter(Burger);



