import React from "react";
import logo from '../../assets/images/burger-logo.png';
import "./Logo.css";

const Logo = (props) => {
  return (
    <div className="Logo" onClick={props.click}>
      <img src={logo} alt="icon" />
    </div>
  );
};

export default Logo;



