import * as actionTypes from "../actions/actionTypes";
import { updateState } from "../utility";

const initialState = {
  ingredients: null,
  totalPrice: 4,
  error: false,
  buildStart: false
};

const INGREDIENT_COST = {
  salad: 0.5,
  cheese: 1,
  meat: 1.5,
  bacon: 1.5
};

const addIngredientAux = (state, action) => {
  const addUpdates = {
    totalPrice: state.totalPrice + INGREDIENT_COST[action.payload.ingredient],
    ingredients: {
      ...state.ingredients,
      [action.payload.ingredient]:
        state.ingredients[action.payload.ingredient] + 1
    },
    buildStart: true
  };
  return updateState(state, addUpdates);
};

const removeIngredientAux = (state, action) => {
  const removeUpdates = {
    totalPrice: state.totalPrice - INGREDIENT_COST[action.payload.ingredient],
    ingredients: {
      ...state.ingredients,
      [action.payload.ingredient]:
        state.ingredients[action.payload.ingredient] - 1
    },
    buildStart: true
  };
  return updateState(state, removeUpdates);
};

const setIngredientsAux = (state, action) => {
  const setUpdates = {
    ingredients: action.ingredients,
    error: false,
    totalPrice: 4,
    buildStart: false
  };
  return updateState(state, setUpdates);
};

const fetchIngredientsFailedAux = (state, action) => {
  return updateState(state, { error: true });
};

const rootReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.ADD_INGREDIENT:
      return addIngredientAux(state, action);
    case actionTypes.REMOVE_INGREDIENT:
      return removeIngredientAux(state, action);
    case actionTypes.SET_INGREDIENTS:
      return setIngredientsAux(state, action);
    case actionTypes.FETCH_INGREDIENTS_FAILED:
      return fetchIngredientsFailedAux(state, action);
    default:
      return state;
  }
  return state;
};

export default rootReducer;



