import React from "react";
import PropTypes from "prop-types";
import "./BurgerIngridients.css";

const BurgerIngridients = props => {
  let ingridient = null;
  switch (props.type) {
    case "bread-bottom":
      ingridient = <div className="BreadBottom" />;
      break;
    case "bread-top":
      ingridient = (
        <div className="BreadTop">
          <div className="Seeds1" />
          <div className="Seeds2" />
        </div>
      );
      break;
    case "meat":
      ingridient = <div className="Meat" />;
      break;
    case "cheese":
      ingridient = <div className="Cheese" />;
      break;
    case "salad":
      ingridient = <div className="Salad" />;
      break;
    case "bacon":
      ingridient = <div className="Bacon" />;
      break;
    default:
      ingridient = null;
  }
  return ingridient;
};

BurgerIngridients.propTypes = {
  type: PropTypes.string.isRequired
};

export default BurgerIngridients;



