import * as actionTypes from "../actions/actionTypes";
import { updateState } from "../utility";

const initialState = {
  orders: [],
  loading: false,
  redirect: false,
  purchased: false
};

const purchaseInitAux = (state, action) => {
  return updateState(state, { purchased: false });
};

const purchaseBurgerStartAux = (state, action) => {
  return updateState(state, { loading: true });
};

const purchaseBurgerSuccessAux = (state, action) => {
  const order = {
    ...action.orderData,
    id: action.orderId
  };
  const updates = {
    loading: false,
    orders: state.orders.concat(order),
    purchased: true
  };
  return updateState(state, updates);
};

const purchaseBurgerFailAux = (state, action) => {
  return updateState(state, { loading: false });
};

const fetchOrdersStartAux = (state, action) => {
  return updateState(state, { loading: true });
};

const fetchOrdersSuccessAux = (state, action) => {
  return updateState(state, { orders: action.orders, loading: false });
};

const fetchOrdersFailAux = (state, action) => {
  return updateState(state, { loading: false });
};

const orderReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.PURCHASE_INIT:
      return purchaseInitAux(state, action);
    case actionTypes.PURCHASE_BURGER_START:
      return purchaseBurgerStartAux(state, action);
    case actionTypes.PURCHASE_BURGER_SUCCESS:
      return purchaseBurgerSuccessAux(state, action);
    case actionTypes.PURCHASE_BURGER_FAIL:
      return purchaseBurgerFailAux(state, action);
    case actionTypes.FETCH_ORDERS_START:
      return fetchOrdersStartAux(state, action);
    case actionTypes.FETCH_ORDERS_SUCCESS:
      return fetchOrdersSuccessAux(state, action);
    case actionTypes.FETCH_ORDERS_FAIL:
      return fetchOrdersFailAux(state, action);
    default:
      return state;
  }
};

export default orderReducer;



