import React, { Component, Fragment } from "react";
import { connect } from "react-redux";

import * as actionCreators from "../../store/actions/index";
import Burger from "../../components/Burger/Burger";
import BuildControls from "../../components/Burger/BuildControls/BuildControls";
import Modal from "../../components/UI/Modal/Modal";
import Spinner from "../../components/UI/Spinner/Spinner";
import OrderSummary from "../../components/OrderSummary/OrderSummary";

class BurgerBuilder extends Component {
  state = {
    showOrderDetails: false,
    loading: false
  };

  updatePurchaseState = ingredients => {
    let sum = Object.keys(ingredients)
      .map(elem => ingredients[elem])
      .reduce((accumulator, currentValue) => accumulator + currentValue, 0);
    return sum > 0;
  };

  showOrderSummaryHandler = () => {
    if (this.props.isAuth) {
      this.setState({ showOrderDetails: true });
    } else {
      this.props.history.push("/auth");
    }
  };

  cancelPurchaseHandler = () => {
    this.setState({ showOrderDetails: false });
  };

  submitPurchaseHandler = () => {
    this.props.onPurchaseInit();
    this.props.history.push("/checkout");
  };

  componentDidMount() {
    this.props.onSetIngredients();
  }

  render() {
    let disabled = { ...this.props.ingredients };
    for (let key in disabled) {
      disabled[key] = disabled[key] <= 0;
    }
    let order = null;
    if (this.state.loading) {
      order = <Spinner />;
    }
    let burger = this.props.error ? (
      <p>Ingredients can't be loaded!</p>
    ) : (
      <Spinner />
    );
    if (this.props.ingredients) {
      burger = (
        <Fragment>
          <Burger ingredients={this.props.ingredients} />
          <BuildControls
            showSummary={this.showOrderSummaryHandler}
            disabled={disabled}
            price={this.props.totalPrice}
            purchasable={this.updatePurchaseState(this.props.ingredients)}
            isAuth={this.props.isAuth}
            handlers={{
              add: this.props.onIngredientAdd,
              remove: this.props.onIngredientRemove
            }}
          />
        </Fragment>
      );
      order = (
        <OrderSummary
          ingredients={this.props.ingredients}
          price={this.props.totalPrice}
          handlers={{
            submit: this.submitPurchaseHandler,
            cancel: this.cancelPurchaseHandler
          }}
        />
      );
    }
    return (
      <Fragment>
        <Modal
          show={this.state.showOrderDetails}
          click={this.cancelPurchaseHandler}
        >
          {order}
        </Modal>
        {burger}
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    ingredients: state.burgerBuilder.ingredients,
    totalPrice: state.burgerBuilder.totalPrice,
    error: state.burgerBuilder.error,
    isAuth: state.auth.token
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onIngredientAdd: ingredient =>
      dispatch(actionCreators.addIngredient(ingredient)),
    onIngredientRemove: ingredient =>
      dispatch(actionCreators.removeIngredient(ingredient)),
    onSetIngredients: () => dispatch(actionCreators.asyncSetIngredients()),
    onPurchaseInit: () => dispatch(actionCreators.purchaseInit())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BurgerBuilder);



